<template>
	<view class="banner">
		<view class="page-section-spacing">
			<swiper class="swiper" 
				autoplay="true" 
				interval="5000" 
				duration="1500"	>
				<swiper-item v-for="(item , index) in homeSlide" :key="index">
					<image :src="item.imgUrl" mode="aspectFill"></image>
				</swiper-item>
			</swiper>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				homeSlide:[]
			}
		},
		methods: {
			getHomeSlideFunc() {
				var _self = this;
				uni.request({
					url: '/static/mock/banner.json',
					success: (res) => {
						_self.homeSlide = res.data.data.swiperList
					}
				});
			}
		},
		mounted(){
			this.getHomeSlideFunc()
		}
		
	}
</script>

<style lang="scss" scoped>
	.banner{
		image{
			width: 375px;
			height: 150px;
		}
		.page-section-spacing{
			padding: 20px 0;
		}
	}
</style>
